























import { Component, Vue, Prop } from 'vue-property-decorator';

@Component({
  components: {},
})

export default class GameListBlock extends Vue {
  @Prop({ type: Array, default: [] }) public readonly itemData!: any[];
  private gameList?: any[] = [];
  private goPage(url: string) {
    this.$router.push(url);
  }
  private getGameImage(img: string) {
    if (img) {
      return require(`@/assets/img/game/${img}`);
    }
    return '';
  }
}
